import React, {useEffect} from "react";
import styles from './releases.module.scss'
import {movieApi} from "../../api/api";
import {useDispatch, useSelector} from "react-redux";
import {FetchMovieAC, FETCH_UPCOMING_MOVIE, setMovieAC} from "../../redux/movie-reducer";
import {getMovie} from "../../redux/selectors";
import {makeStyles, createStyles, Theme} from '@material-ui/core/styles';
import {GridSpacing} from '@material-ui/core/Grid';
import { NavLink } from "react-router-dom";


export const Releases = () => {
    const movie = useSelector(getMovie)
    const movieImgPath = 'https://image.tmdb.org/t/p/w500/'
    console.log(movie)
    const dispatch = useDispatch()
    useEffect(() => {
        dispatch(FetchMovieAC())
    }, [dispatch])

    return (
        <div className={styles.releases}>
            <h2 className={styles.mainTitle}>New Releases</h2>
            <div className={styles.releasesInner}>
                {movie.map((film) => {
                    return (
                            <div className={styles.movieItem} key={film.id}>
                                <span className={styles.voteAverage}>
                            {film.vote_average}
                        </span>
                                <NavLink to={'/details/'+ film.id}>
                                <div className={styles.poster}>
                                    <img src={movieImgPath + film.poster_path} alt="poster"/>
                                </div>
                                </NavLink>

                                <div>
                                    <h4>{film.original_title}</h4>
                                </div>
                            </div>
                    )
                })}
            </div>
        </div>
    )
}