
type movieItemType = {
    adult: boolean
    backdrop_path: string
    genre_ids: any
    id: number
    original_language: string
    original_title: string
    overview: string
    popularity: number
    poster_path: string
    release_date: string
    title: string
    video: boolean
    vote_average: number
    vote_count: number
}
type detailsMovie = {
    adult: boolean
    backdrop_path: string
    belongs_to_collection: any
    budget: number
    genres: any
homepage: string
id: number
imdb_id: string
original_language: string
original_title: string
overview: string
popularity: number
poster_path: string
production_companies: any
production_countries: any
release_date: string
revenue: number
runtime: number
spoken_languages: any
status: string
tagline: string
title: string
video: boolean
vote_average: number
vote_count: number
}
export const SET_MOVIE = 'SET_MOVIE'
export const FETCH_UPCOMING_MOVIE = 'FETCH_UPCOMING_MOVIE'

export const SET_DETAILS = 'SET_DETAILS'
export const FETCH_DETAILS = 'FETCH_DETAILS'

let initialState = {
    upcomingMovie: [] as Array<movieItemType>,
    movieDetail: {} as detailsMovie
}

export const FetchMovieAC = () => {
    return {
        type: FETCH_UPCOMING_MOVIE
    }
}

export const FetchMovieDetailsAC = (movieId: any) => {
    return {
        type: FETCH_DETAILS,
        movieId
    }
}

export const setMovieDetailsAC = (details: any):setMovieDetailsType => {
    debugger
    return {
        type: SET_DETAILS,
        details
    }

}

type setMovieDetailsType = {
    type: string
    details: any
}

type setMovieType = {
    type: string
    movie: any
}

export const setMovieAC = (movie: Array<movieItemType>):setMovieType => {
    return {
        type: SET_MOVIE,
        movie
    }

}

type InitialType = typeof initialState
type ActionType = setMovieType | setMovieDetailsType

const movieReducer = (state: InitialType = initialState, action: any): InitialType => {
    debugger
    switch (action.type) {
        case SET_MOVIE:
            return {
                ...state,
                upcomingMovie: action.movie,
            }
        case SET_DETAILS:
            return {
                ...state,
                movieDetail: action.details
            }
        default:
            return state
    }
}

export default movieReducer